.pageLayout {
  // overflow-x: hidden;

  .navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 40px;
    margin: 0px;
    position: fixed;
    top: 0;
    z-index: 9;

    li {
      list-style: none;
      a {
        border: 1px solid #101010;
        background-color: #fff;
        border-radius: 100px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        box-sizing: border-box;
        padding: 10px 20px;
        text-decoration: none;
        color: #101010;
        transition: all 0.3s ease;
        font-weight: 700;
        &:hover {
          transition: all 0.3s ease;
          background-color: #101010;
          color: #fff;
        }
      }
    }
    .home {
      width: 44px;
    }
  }
}

.slideIn {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #101010;
  transform-origin: top;
}
.slideOut {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #101010;
  transform-origin: bottom;
}
